export default {
    data(){
        return {
            details: this.$store.state.details,
        }
    },
    computed: {
        detail(){
            return this.details.find(v => v.id == this.$route.params.id)
        }
    },
    methods: {
        toShowDetails(){
            
            this.$store.dispatch("showDetail")
            this.$router.push("/about")
            
        }
    }
}
